@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500&display=swap);
body{font-family:'Fira Sans', sans-serif;font-weight:400}.container{max-width:1180px;margin:0 auto;padding-right:20px;padding-left:20px}h1{font-weight:500}li{list-style-type:none}ul{margin-left:0;padding-left:0}a{color:inherit}

.posts__content{display:grid;grid-gap:20px}@media (min-width: 769px) and (max-width: 9999px){.posts__content{grid-template-columns:repeat(4, 1fr)}}@media (min-width: 577px) and (max-width: 768px){.posts__content{grid-template-columns:repeat(2, 1fr)}}@media (min-width: 1px) and (max-width: 576px){.posts__content{grid-template-columns:1fr}}

.header{height:100px;background-color:#f2f2f2}.header__content{height:100%;display:flex;align-items:center}.header__content ul{display:flex;margin:auto 0}.header__content li:not(:last-child){margin-right:20px}

.post{position:relative}.post:hover{cursor:pointer}.post:hover .post__link{box-shadow:0 0 40px rgba(0,0,0,0.1)}.post:hover .post__del-post{opacity:1}.post:hover .post__del-post:hover{background-color:crimson;color:#fff}.post__del-post{width:30px;height:30px;display:flex;align-items:center;justify-content:center;position:absolute;right:-10px;top:-10px;border-radius:15px;font-size:18px;background-color:#fff;color:#000;box-shadow:0 0 20px rgba(0,0,0,0.2);opacity:0;-webkit-transition:all .5s;transition:all .5s}.post__link{display:block;padding:20px;box-sizing:border-box;box-shadow:0 0 40px rgba(0,0,0,0.05);border-radius:20px;text-decoration:none;-webkit-transition:box-shadow .5s;transition:box-shadow .5s}.post__title{font-size:20px;margin-bottom:10px}

.create-post{display:flex;padding:20px;box-sizing:border-box;box-shadow:0 0 40px rgba(0,0,0,0.05);border-radius:20px;-webkit-transition:box-shadow .5s;transition:box-shadow .5s}.create-post:hover{cursor:pointer;box-shadow:0 0 40px rgba(0,0,0,0.1)}.create-post__plus{color:#000;font-size:20px;margin:auto}.create-post__form{margin:0 auto;display:flex;align-items:center;flex-direction:column}.create-post__input{padding:5px 10px;border-radius:4px;border:1px solid;margin-bottom:10px}.create-post__button{padding:5px 20px;background-color:#2179DF;border:none;border-radius:4px;color:#fff;cursor:pointer;-webkit-transition:background-color .5s;transition:background-color .5s}.create-post__button:hover{background-color:#295ab4}

.view-post{margin-bottom:40px}.view-post__content{padding:20px;box-sizing:border-box;box-shadow:0 0 40px rgba(0,0,0,0.05);border-radius:20px}.view-post__title{font-size:20px;margin-bottom:10px}

.comments__temporary-text{margin-bottom:20px}.comments__comment{padding:20px;box-sizing:border-box;box-shadow:0 0 40px rgba(0,0,0,0.05);border-radius:20px;margin-bottom:20px}.comments__form{display:flex;padding:20px;box-sizing:border-box;box-shadow:0 0 40px rgba(0,0,0,0.05);border-radius:20px;margin-bottom:20px}.comments__button{padding:5px 20px;background-color:#2179DF;border:none;border-radius:4px;color:#fff;cursor:pointer;-webkit-transition:background-color .5s;transition:background-color .5s}.comments__button:hover{background-color:#295ab4}.comments__input{width:100%;margin-right:20px;padding:5px 10px;border:1px solid;border-radius:4px}

